// <Desktop-specific layout - secondary navigation never present
main {
    @include media('<tablet') {
        > .standard-content {
            .banner {
                .banner__block-color {
                    display: none;
                }
            }
        }
    }

    @include media('>=tablet', '<small') {
        > .standard-content {
            @include standard-grid();
            grid-column: span 24;

            .banner {
                grid-column: span 24;
            }

            .content {
                grid-column: 4 / span 18;
            }
        }
    }

    .standard-content {
        .content {
            overflow: hidden;
        }
    }
}

// Desktop-specific layout
main {
    @include media('>=small') {
        > .standard-content {
            @include standard-grid();

            grid-column: span 24;

            .banner {
                grid-column: span 24;
                .banner__standfirst {
                    grid-column: 2/-2;
                }
            }

            .content {
                grid-column: 6 / span 14;
            }
        }
    }

    @include media('>=desktop') {
        > .standard-content {
            .content {
                grid-column: 7 / span 12;
            }
        }
    }
}

// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        .navigation--secondary {
            grid-column: 1 / span 6;

            & + .standard-content {
                @include content-grid();

                grid-column: 7 / span 18;

                .banner {
                    grid-column: 1 / span 18;
                    .banner__standfirst {
                        grid-column: 2/-2;
                    }
                }

                .content {
                    grid-column: 2/-2;
                    overflow: hidden;
                }
            }
        }
    }
}

// Generic styles
.standard-content {
    padding-bottom: $measure--small2;

    .content {
        @include media('>=tablet') {
            margin: 0;
        }
    }
}
